





























































































import Vue from 'vue'
import { getUserPages } from '@/services/user'
import { getRoles, alloctRoles, getUserRole } from '@/services/role'
import dayjs from 'dayjs'
import { DatePicker, Form } from 'element-ui'
interface User {
  id: number
  name: string
  phone: string
  portrait: string
  createTime: string
  status: string
}
interface ResUser {
  records: Array<User>
  total: number
}
export default Vue.extend({
  data() {
    return {
      users: [],
      currentPage: 1,
      pageSize: 10,
      loading: false,
      total: 0,
      form: {
        phone: '',
        date: [undefined, undefined]
      },
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker: DatePicker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick(picker: DatePicker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick(picker: DatePicker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      dialogVisible: false,
      currentRow: null,
      roles: [],
      checkedRoleId: []
    }
  },
  filters: {
    formatTime(timestr: string) {
      return dayjs(timestr).format('YYYY-MM-DD HH:mm:ss')
    }
  },
  created() {
    this.loadUsers()
  },
  methods: {
    async loadUsers() {
      this.loading = true
      const { data } = await getUserPages<ResUser>({
        currentPage: this.currentPage,
        pageSize: this.pageSize,
        phone: this.form.phone,
        startCreateTime: this.form.date[0],
        endCreateTime: this.form.date[1]
      });
      (this.users as Array<User>) = data.data.records
      this.total = data.data.total
      this.loading = false
    },
    async loadRoles() {
      const { data } = await getRoles()
      console.log(data)
      this.roles = data.data
    },
    onSubmit() {
      this.currentPage = 1
      this.loadUsers()
    },
    allocRoleDialog(row: any) {
      this.dialogVisible = true
      this.currentRow = row
      this.loadRoles()
      this.loadRolesPermission()
      console.log(row)
    },
    handleSizeChange(val: number) {
      this.pageSize = val
      this.currentPage = 1
      this.loadUsers()
    },
    handleCurrentChange(val: number) {
      this.currentPage = val
      this.loadUsers()
    },
    resetForm(formName: string) {
      (this.$refs[formName] as Form).resetFields()
    },
    handleClose() {
      console.log('close')
      this.dialogVisible = false
    },
    async allocRole() {
      const { data } = await alloctRoles({
        userId: (this.currentRow as any).id,
        roleIdList: this.checkedRoleId
      })
      if (data.code === '000000') {
        this.$message.success('分配成功')
        this.dialogVisible = false
      }
    },
    async loadRolesPermission() {
      const { data: { data: userRoles } } = await getUserRole((this.currentRow as any).id)
      console.log(userRoles)
      this.checkedRoleId = userRoles.map((role: any) => role.id)
    }
  }
})
